.gallery {
  width: 320px;
  margin: 0 auto;

  &__intro {
    width: 320px;
    min-height: 140px;
    margin: 0 auto;
    padding: 45px 41px;
    padding-top: 35px;
    box-sizing: border-box;
    font-size: 14px;
    line-height: 30px;
    text-align: center;
    color: @color-black;
    background-color: white;
  }

  &__list-wrapper {
    width: 100%;
    margin: 0 auto;
  }

  &__image {
    width: 100%;
  }

  &__name {
    margin-left: 14px;
    padding-top: 15px;
    font-size: 14px;
  }

  &__time {
    font-size: 14px;
    font-weight: normal;
  }

  &__description {
    width: 70%;
    margin-left: 13px;
    margin-top: 5px;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 1.7;
  }

  &__link {
    display: block;
    margin-left: 14px;
    padding-bottom: 30px;
    font-size: 11px;
    text-transform: uppercase;
    text-decoration: none;
    color: @color-link;
  }

  &__article {
    display: flex;
    flex-direction: column;
    margin-top: -5px;
    background-color: @color-grey;
    color: @color-black;
  }
}

.gallery__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  .reset-list ();
  padding-top: 30px;
  background-color: white;
}

.gallery__item {
  margin-bottom: 20px;
  padding: 25px;
  padding-top: 0;
  box-sizing: border-box;
  color: @color-black;
  background-color: @color-grey;
}

.gallery__item a {
  padding-bottom: 10px;
}

.gallery__item:nth-child(1) a {
  padding-bottom: 30px;
}

.gallery__item--hit {
  width: 100%;
  padding: 0;

  b,
  p {
    padding-left: 25px;
  }

  a {
    padding-left: 40px;
  }
}

.gallery__hit-wrapper {
  position: relative;
}

.gallery__hit::before {
  content: "панорама дня";
  position: absolute;
  right: 15px;
  padding: 10px 25px 10px 25px;
  font-size: 12px;
  text-transform: uppercase;
  background-color: @color-pink;
}

@media (min-width: @tablet-width) {
  .gallery {
    width: 660px;
  }

  .gallery__article {
    padding-bottom: 1px;
  }

  .gallery__article--hit {
    position: relative;
    padding: 21px;
  }

  .gallery__item {
    width: 300px;
    margin-bottom: 30px;
    padding: 0;
  }

  .gallery__item--hit {
    width: 660px;
    height: 670px;
  }

  .gallery__intro {
    width: 660px;
    height: 230px;
    padding: 100px;
    padding-bottom: 140px;
    font-size: 18px;
  }

  .gallery__list {
    flex-direction: row;
    justify-content: space-around;
    padding-top: 45px;
  }

  .gallery__hit::before {
    top: 50px;
  }

  .gallery__image--hit {
    height: 510px;
  }

  .gallery__name {
    margin-left: 30px;
    padding-top: 30px;
  }

  .gallery__description {
    margin-left: 30px;
  }

  .gallery__link {
    margin-left: 30px;
  }

  .gallery__name--hit {
    margin-left: 25px;
    padding-top: 5px;
  }

  .gallery__description--hit {
    margin-left: 25px;
    margin-top: 0;
  }

  .gallery__link--hit {
    position: absolute;
    top: 25px;
    right: 80px;
  }
}

@media (min-width: @desktop-width) {
  .gallery {
    width: auto;
  }

  .gallery__list-wrapper {
    background-color: white;
  }

  .gallery__intro {
    width: 100%;
    height: 240px;
    padding-left: 200px;
    padding-right: 200px;
  }

  .gallery__list {
    width: 960px;
    margin: 0 auto;
  }

  .gallery__item {
    width: 26%;
    margin-bottom: 60px;
  }

  .gallery__item--hit {
    width: auto;
    margin-bottom: 120px;
  }

  .gallery__image--hit {
    width: 100%;
    height: 100%;
  }

  .gallery__article--hit {
    display: flex;
  }

  .gallery__hit::before {
    right: 100px;
  }
}
