  font-size: 14px;
  text-align: center;
  line-height: 2;
  background-color: white;
}
.posting__image {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}
.posting__tools,
.tools-mobile {
  margin: 0;
  padding: 0;
  list-style: none;
}
.tools-control {
  padding: 30px 20px;
}
.tools-mobile {
  display: flex;
  justify-content: space-around;
  padding: 0 15px;
}
.tools-mobile__img svg {
  fill: #283645;
  opacity: 0.3;
}
.tools-mobile__img:hover svg {
  opacity: 0.5;
}
.tools-mobile__img--active svg {
  fill: #d22856;
  opacity: 1;
}
.posting .button {
  width: 100%;
  height: 59px;
  font-size: 18px;
  line-height: 30px;
  background-color: transparent;
}
.posting .button--post {
  border: 2px solid #d22856;
  color: #d22856;
}
.posting .button--post:hover {
  background-color: #d22856;
  color: white;
}
.posting .button--post:active {
  color: #e5e5e5;
}
.posting .button--cancel {
  border: 2px solid #283645;
  color: #283645;
  opacity: 0.1;
}
.posting .button--cancel:hover {
  opacity: 0.3;
}
.posting .button--cancel:active {
  background-color: #283645;
  color: white;
}
@media (min-width: 660px) {
  .posting {
    width: 660px;
    min-height: 700px;
  }
  .posting .button {
    width: 100%;
    height: 55px;
  }
  .posting__wrapper {
    display: flex;
    flex-wrap: wrap;
  }
  .posting__text {
    width: 100%;
    padding-top: 70px;
    box-sizing: border-box;
    font-size: 18px;
  }
  .posting__image {
    padding-top: 70px;
  }
  .tools-wrapper {
    width: 45%;
    padding: 20px;
    padding-top: 60px;
    box-sizing: border-box;
  }
  .tools-mobile {
    display: none;
  }
  .tools-control {
    margin-top: 65px;
    padding: 0;
  }
}
@media (min-width: 960px) {
  .posting {
    width: 100%;
    min-height: 670px;
  }
  .posting__wrapper {
    width: 960px;
    margin: 0 auto;
  }
  .posting__text {
    width: 100%;
  }
  .posting__image {
    padding: 60px;
    padding-left: 80px;
  }
  .tools-wrapper {
    width: 50%;
  }
  .tools-control {
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
  }
  .tools-control__item {
    width: 190px;
  }
}
.products {
  width: 320px;
  min-height: 450px;
  margin: 0 auto;
  padding-bottom: 15px;
  text-transform: uppercase;
  background-color: #f2f2f2;
}
.products tr {
  height: 60px;
}
.products__list {
  padding-top: 90px;
  /*временно*/
  overflow: hidden;
}
.products__value {
  width: 150px;
}
.products__name {
  margin-top: 18px;
  margin-bottom: 2px;
  font-size: 18px;
}
.products__price {
  margin: 0;
  margin-bottom: 20px;
}
.products__text {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 1;
  text-align: center;
}
.products__icon--yes {
  width: 31px;
  height: 21px;
  background: url("../img/yes-svg.png") no-repeat center center;
}
.products__icon--no {
  width: 27px;
  height: 26px;
  background: url("../img/no-svg.png") no-repeat center center;
}
.products__item {
  width: 90%;
  transform: translateX(-180px);
  margin-left: auto;
  margin-right: auto;
  border-collapse: collapse;
  text-align: center;
  background-color: white;
}
.products__item tr {
  text-align: left;
  color: #283645;
  border: 3px #f2f2f2 solid;
}
.products__item tr:nth-child(1) {
  padding: 15px;
  font-size: 24px;
  text-align: center;
  background-color: #283645;
  color: white;
}
.products__item td {
  border: 3px #f2f2f2 solid;
}
.products__value--hit {
  position: relative;
  background: url("../img/bg-hit.svg") no-repeat;
}
.products__value--hit::before {
  content: "хит";
  position: absolute;
  font-size: 14px;
  left: 15px;
  top: 15px;
  transform: rotate(-45deg);
}
@media (max-width: 659px) {
  tr:nth-child(2) td:nth-child(3),
  tr:nth-child(3) td:nth-child(3),
  tr:nth-child(4) td:nth-child(3) {
    position: relative;
    background-position: 95% 50%;
  }
  tr:nth-child(2) td:nth-child(3)::before,
  tr:nth-child(3) td:nth-child(3)::before,
  tr:nth-child(4) td:nth-child(3)::before {
    content: "Розовый фильтр";
    position: absolute;
    top: 18px;
    left: 18px;
    font-size: 14px;
  }
  tr:nth-child(3) td:nth-child(3)::before {
    content: "Смайлики";
  }
  tr:nth-child(4) td:nth-child(3)::before {
    content: "Комментарии";
  }
  .products__value:nth-child(3) {
    padding: 0 80px 0 80px;
  }
}
.products__value--1 {
  background-color: #f2f2f2;
}
@media (min-width: 660px) {
  .products {
    width: 660px;
    min-height: auto;
  }
  .products__value {
    width: 150px;
  }
  .products__value:nth-child(3) {
    padding: 0;
  }
  .products__item {
    width: auto;
    margin-left: 19px;
    transform: translateX(0);
  }
  .products__list {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .products__name {
    font-size: 16px;
  }
  .products__text {
    padding-left: 10px;
    text-align: left;
    text-transform: none;
  }
  .products__price {
    margin-bottom: 12px;
    font-size: 18px;
  }
  .products tr {
    height: 45px;
  }
  .products__value--hit {
    position: relative;
    background: url("../img/bg-hit-tablet.svg") no-repeat;
  }
  .products__value--hit::before {
    left: 10px;
    top: 10px;
  }
}
@media (min-width: 960px) {
  .products {
    width: 100%;
    margin: 0 auto;
    min-height: 300px;
  }
  .products__item {
    width: 80%;
    margin: auto;
    margin-top: 120px;
  }
  .products__value {
    height: 60px;
  }
  .products__item tr:nth-child(1) {
    height: 95px;
  }
  .products__text {
    padding-left: 25px;
    font-size: 18px;
  }
  .products__list {
    padding-top: 30px;
  }
}
.slider-controls {
  position: relative;
  margin: 0 auto;
  margin-top: 25px;
  width: 60px;
  height: 50px;
}
.slider-toggle {
  display: none;
}
.slider-controls__item--1::before {
  content: "";
  position: absolute;
  top: 3px;
  left: -1px;
  width: 6px;
  height: 6px;
  background-color: white;
  border: 3px solid #283645;
  border-radius: 50%;
}
.slider-controls__item--2::after {
  content: "";
  position: absolute;
  top: 6px;
  left: 27px;
  width: 6px;
  height: 6px;
  background-color: grey;
  opacity: 0.3;
  border-radius: 50%;
}
.slider-controls__item--3::after {
  content: "";
  position: absolute;
  top: 6px;
  left: 53px;
  width: 6px;
  height: 6px;
  background-color: grey;
  opacity: 0.3;
  border-radius: 50%;
}
.slider-controls--products {
  margin-top: 35px;
}
.slider-controls__item:hover::after {
  opacity: 0.6;
}
.slider-controls__item:active::after {
  opacity: 0.8;
}
.slider-controls__item--2::before {
  display: none;
  content: "";
  position: absolute;
  left: 25px;
  width: 6px;
  height: 6px;
  background-color: white;
  border: 4px solid #283645;
  border-radius: 50%;
}
.slider-controls__item--3::before {
  display: none;
  content: "";
  position: absolute;
  right: 0;
  width: 6px;
  height: 6px;
  background-color: white;
  border: 4px solid #283645;
  border-radius: 50%;
}
@media (min-width: 660px) {
  .slider-controls--products {
    display: none;
  }
}
@media (min-width: 960px) {
  .slider-controls--feedback {
    display: none;
  }
}
.footer {
  width: 100%;
  margin: 0 auto;
  background-color: white;
}
.footer__logo {
  margin: 0;
  padding: 0;
  list-style: none;
}
.footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.logo-footer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo-footer__item:hover path {
  fill: #d22856;
}
.logo-footer__item:active {
  opacity: 0.3;
}
.logo-footer-desktop {
  display: none;
}
.footer--none {
  display: none;
}
.logo-footer__image {
  width: 150px;
  height: 80px;
  padding-top: 40px;
}
.logo-footer__image-letters path:hover {
  fill: #d22856;
}
.logo-footer__image-letters path:active {
  fill: #283645;
  opacity: 0.3;
}
@media (min-width: 660px) {
  .footer--none {
    display: block;
  }
  .footer {
    width: 100%;
  }
  .footer__wrapper {
    flex-direction: row;
    padding: 30px 0 30px 0;
    padding-top: 5px;
  }
  .footer__logo {
    padding-left: 25px;
  }
}
@media (min-width: 960px) {
  .footer {
    width: 100%;
  }
  .footer__wrapper {
    width: 960px;
    margin: 0 auto;
    padding: 60px 0 60px 0;
  }
  .logo-footer-mobile {
    display: none;
  }
  .logo-footer-desktop {
    display: block;
  }
  .logo-footer__image {
    margin-left: 0;
    padding-top: 15px;
  }
}
.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  width: 180px;
  min-height: 100%;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
}
.social__item {
  position: relative;
  width: 46px;
  height: 46px;
  border: 3px solid #f2f2f2;
  border-radius: 50%;
}
.social__item svg {
  position: absolute;
  top: 15px;
  left: 15px;
}
.social__item path {
  fill: #d22856;
}
.social__item:hover {
  border: 3px solid #283645;
}
.social__item:active {
  opacity: 0.3;
}
@media (min-width: 660px) {
  .social {
    margin-right: 10px;
  }
  .social__list {
    padding-top: 8px;
  }
}
@media (min-width: 960px) {
  .social {
    padding-bottom: 30px;
  }
}
.copyright {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 150px;
  padding-top: 30px;
  font-size: 14px;
  color: #283645;
}
.copyright a {
  display: block;
  margin: 0;
  padding: 0;
  text-decoration: none;
}
.copyright path {
  fill: #d22856;
}
.copyright:hover {
  cursor: pointer;
}
.copyright:hover path {
  fill: #283645;
}
.copyright:active {
  opacity: 0.3;
}
.copyright__text {
  text-align: left;
}
@media (max-width: 659px) {
  .copyright {
    margin: 0 auto;
  }
}
@media (min-width: 660px) {
  .copyright {
    margin-right: 35px;
    padding-top: 18px;
  }
  .copyright a {
    margin-left: 15px;
  }
}
@media (min-width: 960px) {
  .copyright {
    padding-bottom: 30px;
  }
}
